:root {
  --main-font: 'Roboto', sans-serif;
  --alternate-font: sans-serif;
  --font-size: 16px;
  --font-weight-300: 100;
  --font-weight-400: 400;
  --font-weight-500: 500;
  --font-weight-700: 700;
  --line-height: 1.6;
  --main-color: #FFF;
  --color-hover: #6f007b;
  --color-admin: #e4e4e4;
  --main-modal-color: rgb(255, 255, 255, .75);
  --second-color: #000;
  --three-color: #fbfbfb;
  --second-modal-color: rgba(0, 0, 0, .75);
  --alternate-color: rgb(190, 0, 143);
  --alternate-modal-color: rgba(68, 0, 95, 0.75);
  --bg-color: var(--main-color);
  --bg-modal-color: var(--main-modal-color);
  --bg-alternate-color: #999;
  --bg-alternate-modal-color: rgba(153, 153, 153, .75);
  --border-color: #DDD;
  --text-color: #242424;
  --title-color: #333;
  --link-color: var(--alternate-color);
  --link-hover-color: var(--alternate-modal-color);
  --max-width: 1600px;
  --header-height: 5rem;
  --logo-width: 4rem;
  --logo-height: 4rem;
  --logo-spacing: 1rem;
  --z-back: -10;
  --z-standar: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

html {
  box-sizing: border-box;
  font-family: var(--main-font),var(--alternate-font);
  font-size: var(--font-size);
  line-height: var(--line-height);
  color: var(--text-color);
  background-color: var(--bg-modal-color);
}

*,
*:after,
*:before {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  overflow-x: hidden;
}

a {
  font-weight: var(--font-weight-300);
  text-decoration: none;
  color: var(--link-color);
  transition: all .3s ease-out;
}

@media screen and (min-width: 37.5em) {
  a a {
    font-weight: var(--font-weight-300);
    text-decoration: none;
    color: var(--link-color);
    transition: all .3s ease-out;
  }

  a a:hover {
    color: var(--link-hover-color);
  }
}

@media screen and (min-width: 64em) {
  a:hover {
    color: var(--link-hover-color);
  }
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
}

::selection {
  background-color: var(--main-color);
  color: var(--second-color);
}

.u-responsive-grid {
  display: grid;
  grid-gap: 1em;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.u-responsive-flexbox {
  display: flex;
  flex-wrap: wrap;
}

.u-responsive-flexbox>* {
  flex: 1 1 200px;
}

.u-message,
.About-info,
.Sign-btn,
.Form input[type="submit"],
.Form progress+span {
  font-size: 4vw;
  font-weight: bold;
  text-align: center;
  font-family: "Roboto",sans-serif;
}

@media screen and (min-width: 45em) {
  .u-message,
  .About-info,
  .Sign-btn,
  .Form input[type="submit"],
  .Form progress+span {
    font-size: 1.5rem;
  }
}

.u-none {
  display: none;
  font-family: "Roboto",sans-serif;
}

.u-btn,
.Sign-btn,
.Form input[type="submit"] {
  margin: 1rem auto;
  padding: .5rem 1rem;
  font-family: "Roboto",sans-serif;
  outline: 0;
  border: 0;
  font-weight: 400;
  border-radius: 50px;
  font-size: 15px;
  background-color: #000;
  color: #fff;
  cursor: pointer;
  transition: all .3s ease;
}

.u-btn:hover,
.Sign-btn:hover,
.Form input:hover[type="submit"] {
  background-color: var(--alternate-color);
  font-family: "Roboto",sans-serif;
}

.main-grid {
  margin-top: 49px;
  font-family: "Roboto",sans-serif;
}

.l-container {
  max-width: 1200px;
  font-family: "Roboto",sans-serif;
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  flex-wrap: wrap;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000000;
  padding: 5px;
}

.footer-title {
  font-weight: 300;
  color: #fff;
  font-size: 15px;
}

.footer-year {
  font-weight: 300;
  color: #fff;
  font-size: 15px;
}

.About {
  margin: 0 auto;
  max-width: 1024px;
}

.About-header {
  margin: 2rem auto;
  width: 80%;
  display: grid;
  grid-template-columns: 1fr repeat(2, 2rem) 1fr;
}

.About-header img {
  border-radius: 50%;
}

.About-header img:first-child {
  grid-column: 1 / 4;
  grid-row: 1 / 2;
}

.About-header img:last-child {
  grid-column: 2 / 5;
  grid-row: 1 / 2;
}

.About-info {
  width: 80%;
  margin: 4rem auto;
  text-align: left;
}

.About-flow {
  width: 90%;
  margin: 2rem auto;
}

.About-flow div {
  margin-bottom: 4rem;
  display: grid;
  grid-template-columns: 3rem 1fr;
  grid-template-rows: repeat(2, auto);
  grid-template-areas: 'h2 h3' '.  p';
  grid-gap: 2vw;
  align-items: baseline;
}

@media screen and (min-width: 64em) {
  .About-flow div {
    grid-gap: 1rem;
  }
}

.About-flow h2 {
  grid-area: h2;
  color: var(--title-color);
  font-size: 10vw;
}

@media screen and (min-width: 64em) {
  .About-flow h2 {
    font-size: 5rem;
  }
}

.About-flow h3 {
  grid-area: h3;
  color: var(--title-color);
  font-size: 5vw;
}

@media screen and (min-width: 64em) {
  .About-flow h3 {
    font-size: 2.5rem;
  }
}

.About-flow p {
  grid-area: p;
  font-size: 4vw;
}

@media screen and (min-width: 37.5em) {
  .About-flow p {
    font-size: 3.5vw;
  }
}

@media screen and (min-width: 45em) {
  .About-flow p {
    font-size: 3vw;
  }
}

@media screen and (min-width: 64em) {
  .About-flow p {
    font-size: 1.5rem;
  }
}

.Contact {
  margin: 0 auto;
  max-width: 60%;
  min-height: calc(100vh - var(--header-height));
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 45em) {
  .Contact {
    min-height: calc(100vh - var(--header-height) - 2rem);
  }
}

.Contact Form {
  width: 100%;
  margin-top: 26px;
}

.Admin {
  padding: 1rem;
  min-height: calc(100vh - var(--header-height));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 45em) {
  .Admin {
    min-height: calc(100vh - var(--header-height) - 2rem);
  }
}

.Admin-auth {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Admin-auth>* {
  width: 80%;
}

@media screen and (min-width: 45em) {
  .Admin-auth {
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
  }

  .Admin-auth>* {
    width: 40%;
  }
}

.Admin-user {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Admin-user img {
  max-width: 150px;
  border-radius: 50%;
}

.Sign-btn {
  max-width: 50%;
}

.Header {
  position: fixed;
  z-index: var(--z-standar);
  top: 0;
  padding-top: var(--header-height);
  margin-left: 0;
  padding-top: 0;
  padding: .5rem;
  width: 100%;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
}

.logo {
  position: fixed;
  z-index: 999;
  top: var(--logo-spacing);
  left: var(--logo-spacing);
  padding: var(--logo-spacing);
  width: var(--logo-width);
  height: var(--logo-height);
  padding-top: 0;
}

@media screen and (min-width: 45em) {
  .logo {
    position: static;
    width: var(--header-height);
    height: var(--header-height);
    background-color: transparent;
    border-radius: 0;
    padding-top: 0;
  }
}

.Footer {
  text-align: center;
  background-color: var(--second-modal-color);
  color: var(--alternate-color);
}

@media screen and (min-width: 45em) {
  .Footer {
    display: none;
  }
}

@media screen and (min-width: 64em) {
  .Footer {
    display: none;
  }
}

