.Form * {
  margin-bottom: .5rem;
}

.Form input[type="text"],
.Form input[type="email"],
.Form textarea {
  padding: .5rem;
  width: 100%;
  display: block;
  border: thin solid var(--alternate-color);
  border-radius: .25rem;
  font-size: 1rem;
  font-family: var(--main-font);
  transition: all .3s ease-out;
}

.Form progress {
  margin-bottom: 1rem;
  -webkit-appearance: none;
  appearance: none;
  border: 0;
  width: 100%;
  height: 2rem;
}

.Form progress[value] {
  background-color: var(--alternate-color);
}

.Form progress::-webkit-progress-bar {
  background-color: var(--alternate-color);
}

.Form progress::-moz-progress-bar {
  background-color: var(--second-color);
}

.Form progress::-webkit-progress-value {
  background-color: var(--second-color);
}

.Form progress+span {
  display: block;
}

.Form-uploader {
  position: relative;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Form-uploader span {
  padding-left: .5rem;
  width: 15%;
  display: block;
  text-align: center;
}

.Form-uploader input[type="file"] {
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  cursor: pointer;
  opacity: 0;
}

.menu {
  display: flex;
  align-items: center;
  list-style: none;
  padding-left: 75px;
